import {api} from 'ts/util/api';


api({
	getCarousel: 'canrousel/GetAllCanrousel',
	getAnnouncement: 'Information/QueryAllEnableInformations?type=1&pageIndex=0&pageSize=2',
	getSubjects: 'Information/InformationById',
});
api.getSubjects.set('unlimited', true);


api.getCarousel(data => {
	document.getElementById('swiper').innerHTML = template('tpl-swiper', data);
	new Swiper('#swiper', {
		autoplay: {
			delay: 2500,
			disableOnInteraction: false,
		},
		navigation: {
			nextEl: '.swiper-button-next',
			prevEl: '.swiper-button-prev',
		},
		pagination: {
			el: '.swiper-pagination',
		},
	});
});


api.getAnnouncement(data => {
	let results = data.results;
	if (results && results.length) {
		getSubject(results, (list) => {
			console.log(list);
			document.getElementById('an1').innerHTML = template('tpl-news', list);
		});
	}
});


function getSubject(list: Array, callback: Function) {
	let i = 0, l = list.length, n = 0;
	for (; i < l; i++) {
		let an = list[i];
		api.getSubjects({informationId: an.id}, data => {
			an['subjects'] = data;
			if (++n === l) {
				callback(list);
			}
		});
	}

}
